AFAFA;\n$sidebar-color: #F4F4F4;\n$task-complete-color: lightgray;\n$box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);\n\n* {\n  box-sizing: border-box;\n}\n\nheader  {\n  background-color: $primary-color;\n  color: $secondary-color;\n  font-family: 'Roboto', sans-serif;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin: 0;\n  font-weight: 700;\n  font-size: 20px;\n  padding: 0.7rem 3rem;\n}\n\nbody {\n  background-color: $secondary-color;\n  font-family: 'Roboto', sans-serif;\n  display: flex;\n  flex-direction: column;\n  height: 100vh;\n  overflow: hidden;\n  margin: 0;\n  padding: 0;\n}\n\nmain {\n  display: flex;\n  height: 100vh;\n}\n\n#project {\n  flex: 4 0 auto;\n  overflow: auto;\n}\n\n#sidebar {\n  background-color: $sidebar-color;\n  flex: 0 0 auto;\n  width: 300px;\n    padding: 30px 10px 0px 30px;\n}\n\n.material-icons {\n  cursor: pointer;\n  padding: 2px;\n  border-radius: 5px;\n  &:hover {\n    background-color: rgb(184,\n      184,\n      184, 0.5);\n  }\n}\n\n.material-icons.md-24 {\n  font-size: 24px;\n}\n\nbutton {\n  background-color: $primary-color;\n  color: $secondary-color;\n  border: 0;\n  border-radius: 10px;\n  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);\n  padding: 14px 40px;\n  font-size: 16px;\n  cursor: pointer;\n\n  &:active {\n    transform: scale(0.98);\n  }\n\n  &:focus {\n    outline: 0;\n  }\n}\n\n.project {\n  cursor: pointer;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 10px 20px;\n  border-radius: 10px;\n  &:hover {\n    background-color: rgb(184,\n          184,\n          184, 0.5);\n  }\n}\n\n.current {\n  background-color: rgb(184,\n        184,\n        184, 0.5);\n}\n\n.project-count {\n  color: rgb(150,\n        150,\n        150);\n  font-size: 0.9rem;\n}\n\nh3 {\n  padding: 0;\n  margin: 0;\n  margin-bottom: 15px;\n}\n\n#project {\n  padding: 35px 50px;\n}\n\n.task {\n  cursor: pointer;\n\n  display: flex;\n  gap: 15px;\n  align-items: center;\n  padding: 10px 0;\n}\n\n.task-date {\n  color: $primary-color;\n  margin-left: auto;\n}\n\n.task-detail {\n  display: flex;\n  flex-direction: column;\n}\n\n.task-desc {\n  margin-top: 10px;\n  color: rgb(150,\n      150,\n      150);\n      font-size: 0.8rem;\n}\n\nhr {\n  border-color: rgb(150,\n      150,\n      150, 0.1);\n  margin: 0 0 10px 0;\n}\n\n.priority-1 {\n  border: tomato 2px solid;\n  padding: 10px;\n  border-radius: 15px;\n\n  &:hover {\n      background-color: $task-complete-color;\n    }\n}\n\n.priority-2 {\n  border: orange 2px solid;\n  padding: 10px;\n  border-radius: 15px;\n\n  &:hover {\n    background-color: $task-complete-color;\n  }\n}\n\n.priority-3 {\n  border: royalblue 2px solid;\n  padding: 10px;\n  border-radius: 15px;\n\n  &:hover {\n      background-color: $task-complete-color;\n    }\n}\n\n.priority-4 {\n  border: lightgray 2px solid;\n  padding: 10px;\n  border-radius: 15px;\n\n  &:hover {\n      background-color: $task-complete-color;\n    }\n}\n\nform {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\nform > div {\n  display: flex;\n  gap: 20px;\n}\nform > div > div:nth-child(1) {\n  display: flex;\n  gap: 10px;\n  flex-direction: column;\n}\n\nform>div>div:nth-child(2) {\n  display: flex;\n  gap: 20px;\n  flex-direction: column;\n}\n\ndialog {\n  border: none;\n  border-radius: 15px;\n  box-shadow: $box-shadow;\n  margin-top: 100px;\n}\n\n#cancel-task {\n  background-color: lightgrey;\n  color: #181818;\n}\n\n#task-title{\n  font-size: 19px;\n}\n\n#task-title, #task-description {\n  outline: none;\n  border: none;\n}\n\n#task-pickable-values-add {\n  display: flex;\n  justify-content: space-between;\n}\n\n#edit-dialog {\n  padding: 0;\n}\n#edit-header {\n  padding: 16px;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 700px;\n}\n\n#edit-container {\n  display: flex;\n  min-height: 200px;\n  width: auto;\n}\n\n#addit